
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
name: "component_name",
components: {
XDialog
},
props: {
showToast: {
type: Boolean,
default: false
},
checkListBox: {
type: Object,
default: () => {
return {}
}
}
},
data() {
return {
}
},
methods: {

showCheckText(item) {
return item.status === 1 ? item.textSuc : item.textErr
},
}
}



